.custom-button {
    //min-width: 165px;
    width: 80%;
    height: 50px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 20px 0 20px;
    font-size: 15px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-family: 'Open Sans Condensed';
    font-weight: bolder;
    border: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
  
    &:hover {
      background-color: white;
      color: black;
      border: 1px solid black;
    }

    &.google-sign-in{
      background-color: #4285f4;
      

      &.hover{
        background-color:#357ae8; 
        border: none;

      }
    }

    &.inverted{ 
      background-color: white;
      color: black;
      border: 1px solid black;

      &:hover{
        background-color: black;
        color: white;
        border: none;
      }
    }
  }